@import '_vars'

*
    transition: 0.2s
    font-family: var(--sans-font)
    box-sizing: border-box

    &:focus
        outline: none

html
    scroll-behavior: smooth

body
    color: var(--text)
    background: var(--bg)
    font-size: 1.15rem
    line-height: 1.5
    margin: 0

body:not(.no-grid)
    display: grid
    grid-template-columns: 1fr min(45rem, 90%) 1fr

body:not(.no-grid)>*
    grid-column: 2

p
    font-size: 18px

button
    cursor: pointer

/* ================ HEADER ================ */

header 
    text-align: center
    position: relative
    background: var(--accent-bg)

    align-items: center
    justify-content: center
    z-index: 40

    >img
        position: absolute
        top: 0
        left: 0
    
        width: 100%
        height: 300px

    &.tall
        padding-top: 3rem
        padding-bottom: 3rem

    &.with-nav
        // padding-top: 4.5rem  

    &:not(.no-grid) 
        grid-column: 1 / -1

    h1 
        max-width: 1200px
        margin: 1rem auto

    p 
        max-width: 40rem
        margin: 1rem auto

/* ================ MAIN ================ */

hr
    width: 30%
    opacity: 0.2

section.highlight
    *
        text-align: left

    > i
        left: 1rem
        top: 1rem
        position: absolute
        opacity: 0.5

    margin-top: 2rem

    // position: relative
    background: var(--accent-bg)
    box-shadow: 0 0 0 100vmax var(--accent-bg)
    clip-path: inset(0 -100vmax)
    padding: 1rem

    h3
        margin-block: 0

    button
        color: var(--text)
        background: var(--glass)

main
    padding-top: 1.5rem

footer 
    margin-top: 4rem
    padding: 2rem 1rem 1.5rem 1rem
    color: var(--text-light)
    font-size: 0.9rem
    text-align: center
    opacity: 0.8

div.row
    display: flex
    width: 100%

h1, h2, h3
    font-weight: 600
    line-height: 1.1

/* Reduce header size on mobile */
@media (max-width: 720px)
    h1
        font-size: 2.5rem
    h2
        font-size: 2.1rem
    h3
        font-size: 1.75rem
    h4
        font-size: 1.25rem

/* Format links & buttons */
main, footer
    a:not(.plain)
        color: var(--accent)
        text-decoration: 1px underline var(--glass) !important
        border-radius: 3px
        outline: 2px solid transparent

        &:hover
            outline-color: var(--soft)
            background: var(--soft)

a.plain, footer a.plain,
a.plain:visited, footer a.plain:visited
    color: var(--text)
    text-decoration: none

textarea:hover, input:hover, select:hover
    opacity: 0.9

button, [role="button"], input[type="submit"], input[type="reset"], input[type="button"]
    border: none
    border-radius: var(--edge)
    font-size: 1rem

    padding: 0.7rem 0.9rem
    margin: 0.5rem 0

    color: var(--bg)
    background: var(--accent)
    box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.2), 0 0px 20px 0 rgba(0, 0, 0, 0.19)

    &:hover
        background: var(--active) !important

*[disabled]
    opacity: 0.5
    cursor: not-allowed

input:disabled, textarea:disabled, select:disabled
    cursor: not-allowed
    background-color: var(--disabled)

input[type="range"]
    padding: 0

abbr
    cursor: help

details
    background: var(--accent-bg)
    border-radius: var(--edge)
    margin-bottom: 1rem

summary
    user-select: none
    cursor: pointer
    padding: 0.6rem 1rem

details[open]
    padding: 0.6rem 1rem 0.75rem 1rem

    summary
        margin-bottom: 0.5rem
        padding: 0

    &+*
        margin-top: 0

details[open]>*:last-child 
    margin-bottom: 0

table
    border-collapse: collapse
    width: 100%
    margin: 1.5rem 0

td, th
    text-align: left
    padding: 0.5rem

th
    background: var(--accent-bg)
    font-weight: bold

table caption
    font-weight: bold
    margin-bottom: .5rem

textarea, select, input
    font-size: inherit
    font-family: inherit
    padding: 0.5rem
    margin-bottom: 0.5rem
    color: var(--text)
    background: var(--accent-bg)
    border: none
    border-radius: var(--edge)
    box-shadow: none
    -moz-appearance: none
    -webkit-appearance: none
    appearance: none

select, input[type="checkbox"], input[type="radio"]
    cursor: pointer

/* Drop-down arrow */
select
    background-image: linear-gradient(45deg, transparent 49%, var(--text) 51%), linear-gradient(135deg, var(--text) 51%, transparent 49%)
    background-position: calc(100% - 20px), calc(100% - 15px)
    background-size: 5px 5px, 5px 5px
    background-repeat: no-repeat

select[multiple]
    background-image: none

input[type="checkbox"], input[type="radio"]
    vertical-align: bottom
    position: relative

input[type="radio"]
    border-radius: 100%

input:checked
    background: var(--accent)

    &::after
        content: " "
        width: 0.1em
        height: 0.25em
        border-radius: 0
        position: absolute
        top: 0.05em
        left: 0.18em
        background: transparent
        border-right: solid var(--bg) 0.08em
        border-bottom: solid var(--bg) 0.08em
        font-size: 1.8em
        transform: rotate(45deg)

textarea
    width: 80%

@media (max-width: 720px)
    textarea, select, input
        width: 100%

input[type="checkbox"], input[type="radio"]
    width: auto
    margin: 0.4rem

input[type="file"]
    border: 0
    background-color: var(--accent-bg)

main img, main video
    max-width: 100%
    height: auto
    border-radius: var(--edge)
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 50px 50px 0 rgba(0, 0, 0, 0.308)
